.modal {
  display: flex;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  border: 1px solid rgba(76, 76, 255, 0.2);
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04),
    0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
}
.modal__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 720px;
  width: 100%;
  border-radius: 40px;
  background-color: #1c1c21;
  z-index: 2000;
  margin: auto;
}
.modal__title__yes {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.modal__title__not {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
}
.modal__close {
  cursor: pointer;
}
