.wrapper {
  flex-direction: column;
  width: 100%;
  max-width: 600px;
  height: calc(100vh - 88px);
  margin-left: auto;
}

.list_items {
  flex-direction: column;
  gap: 16px;
  max-height: 464px;
  overflow-y: overlay;
  width: 100%;
}

.item {
  gap: 8px;
  width: 100%;
}

.wrapper_total {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 64px;
}

.modal_hidden {
  width: 400px;
  height: 400px;
  overflow: hidden;
}

.grow_none {
  flex-grow: 0;
}

.full_center {
  text-align: center;
  width: 100%;
}
